import React from "react";
import { T_StringOrNumber } from "shared/types/common";
import { textsChit } from "../constants";
import "../styles.scss";

export default function ChitOutput({
  className,
  title,
  results
}: {
  className?: string;
  title: string;
  results: {
    text: string;
    value?: T_StringOrNumber;
    valueClassName?: string;
  }[];
}) {
  return (
    <section className={`chit-output-section ${className ?? ""}`}>
      <h2>{title}</h2>
      <output>
        <table>
          <tbody className="chit-output-results-table-body">
            {results.map(({ text, value, valueClassName }) => (
              <tr key={text}>
                <th className="chit-output-text">{text}</th>
                <td className={valueClassName}>{value ?? textsChit.noValue}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </output>
    </section>
  );
}
